@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

/* colors */
/* elements height */
.image-size {
  max-width: 100%;
  max-height: calc(calc((100vh - 3.75rem - calc(2*0.625rem))/ 3 - calc(0.3125rem*4)) - calc(0.3125rem * 2));
}

#go-to-search {
  background-color: #262626;
  color: white;
  padding: 0.625rem;
  margin: 1.25rem;
  border-radius: 0.3125rem;
}

.city-name-text {
  position: absolute;
  margin: 0.625rem 0 0.625rem 0.625rem;
  padding: 0.625rem;
  bottom: 1.25rem;
  right: 0;
  border-radius: 0.3125rem 0 0 0.3125rem;
  color: #262626;
  font-weight: bold;
}

/*# sourceMappingURL=home.css.map */

/* colors */
/* elements height */
#side-navigation {
  position: absolute;
  right: 0;
  top: 0;
  min-height: calc(100vh - 3.75rem);
  width: 6.25rem;
}
#side-navigation .active {
  right: 0;
  background-color: #d4e7f7;
  color: #262626;
  font-weight: bold;
}

#side-navigation a,
#side-navigation button {
  position: absolute;
  color: white;
  right: -3.125rem;
  transition: 0.5s;
  padding: 0.625rem;
  margin: 0;
  width: 6.25rem;
  text-decoration: none;
  font-size: 1.125rem;
  border-radius: 0.3125rem 0 0 0.3125rem;
  z-index: 10;
}

#side-navigation a:hover,
#side-navigation button:hover {
  right: 0;
}

#home-navlink-sidenav {
  top: 0.9375rem;
}

#search-navlink-sidenav {
  top: 4.6875rem;
}

#about-navlink-sidenav {
  top: 8.4375rem;
}
#about-navlink-sidenav.active {
  background-color: #e9b563;
}

#gallery-navlink-sidenav {
  top: 12.1875rem;
}

#contacts-navlink-sidenav {
  top: 15.9375rem;
}

#login-button {
  top: 19.6875rem;
  border: none;
}

/*# sourceMappingURL=navigation.css.map */

/* colors */
/* elements height */
footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

.footer-height {
  height: 3.75rem;
}

#footer .nav-link {
  margin: 0 0.625rem 0 0;
  padding: 0.3125rem 0.3125rem 0.3125rem 0;
  color: white;
}
#footer .nav-link:hover {
  text-decoration: underline;
}

/*# sourceMappingURL=footer.css.map */

/* colors */
/* elements height */
html, #root, body {
  min-height: 100vh;
  width: 100%;
  font-family: "Quicksand", "Arial", sans-serif;
  margin: 0;
  padding: 0;
}

#root {
  position: relative;
}

.main-container, .main-row {
  height: calc(100vh - 3.75rem);
}

.row-height {
  height: calc((100vh - 3.75rem - calc(2*0.625rem))/ 3 - calc(0.3125rem*4));
}

.row-m-p {
  margin: 0 0 0.625rem 0;
  padding: 0.3125rem;
}

.col-m-p {
  margin: 0.625rem;
  padding: 0.3125rem;
}

.dark-color-bg {
  background-color: #262626;
}

.gray-color-bg {
  background-color: #666666;
}

.blue-color-bg {
  background-color: #d4e7f7;
}

.beige-color-bg {
  background-color: #fce6cf;
}

.gold-color-bg {
  background-color: #e9b563;
}

.relative {
  position: relative;
}

